




//- иконки
%icon {
	content: '';
	width: 1em;
	height: 1em;
}


%background-icon {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: contain;
	max-width: 512px;
	max-height: 512px;
}


%icon-angle { background-image: url( 'data:image/svg+xml;base64,' ); }
%icon-double-arrow { background-image: url( 'data:image/svg+xml;base64,' ); }
%icon-close { background-image: url( 'data:image/svg+xml;utf8;base64,' ); }
%icon-home { background-image: url( 'data:image/svg+xml;base64,' ); }



